body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


nav.navbar.navbar-default {
  margin-bottom: 0;
}
.navbar{
  position: fixed;
  display: block;
  width: 100%;
  min-height: 70px;
  white-space: nowrap;
  box-sizing: border-box;
  background-color:rgb(216, 214, 218);
  color: rgb(235, 32, 157);
  z-index: 99;
  
}
.navbar li{
  display: inline-block;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 33.3%;
  text-align: center;
  font-family: 'ZCOOL QingKe HuangYou', cursive;
}
.brand h4{
  font-weight: bolder;
  font-family: 'Wallpoet', cursive;
}

.header{
  min-height: 400px;
  background-color: gray;
  background-image: url(data:image/png;base64,);
  color:white;
  display: block;
  text-align: center;
  width: 100%;
}
.header h1{
  padding-top: 4em;
  font-family: 'ZCOOL QingKe HuangYou', cursive;
}
.header h2{
  font-family: 'ZCOOL QingKe HuangYou', cursive;
}
.wrapper {
  
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  
  -webkit-justify-content: space-around;
  
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  background:#6CADDC;
}
.img-container img{
  object-fit: cover;
  height: 200px;
  width: 200px;
}


